@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:'Poppins', sans-serif}.container{width:90vw;margin:auto}

.nav{display:flex;flex-direction:row;justify-content:space-between;margin-left:4vw;padding-top:40px;position:fixed;width:90%;background-color:white;z-index:99}.nav .navTitle{display:flex;flex-direction:row;align-items:baseline}.nav .navTitle div{color:#828282;margin-right:20px}.nav .navTitle div:first-child{font-weight:700;font-size:24px;color:#000}.nav .navItems ul{display:flex;flex-direction:row;list-style:none;align-items:center}.nav .navItems ul li{cursor:pointer;margin:0 10px;padding:10px 30px;transition:all 0.2s ease}.nav .navItems ul .selected{background-color:#000;padding:10px 30px;border-radius:4px;color:white;transition:all 0.2s ease}

h1{font-size:36px}.insightsContainer{padding-top:200px}.insightsContainer .sectionContainer{display:flex;flex-direction:row;margin-bottom:100px}.insightsContainer .sectionContainer .vizContainer{width:60%;position:relative;margin-bottom:20px}.insightsContainer .sectionContainer .vizContainer div::after{content:' ';height:40px;width:100%;background-color:white;display:block;position:absolute;top:573px}.insightsContainer .sectionContainer .indigenous div::after{height:40px}.insightsContainer .sectionContainer .descContainer{width:40%;padding-left:100px}.insightsContainer .sectionContainer .descContainer h3{margin-top:0}.insightsContainer .sectionContainer .descContainer ul li{margin:10px 0}.insightsContainer .factorContainer{margin-bottom:100px}.insightsContainer .factorContainer ul{display:flex;flex-direction:row;list-style-type:none;padding:0}.insightsContainer .factorContainer ul li{font-weight:400;font-size:18px;margin:10px;padding:5px 20px;background-color:#5779a3;border-radius:4px;color:white}.insightsContainer .factorContainer ul li:nth-child(2){background-color:#d1605e}.insightsContainer .factorContainer ul li:nth-child(3){background-color:#679f9c}.insightsContainer .factorContainer ul li:nth-child(4){background-color:#ceb459}.insightsContainer .factorContainer ul li:nth-child(5){background-color:#a87d9f}table.greyGridTable{border:2px solid #ffffff;width:60%;text-align:center;border-collapse:collapse}table.greyGridTable td,table.greyGridTable th{border:1px solid #ffffff;padding:3px 4px}table.greyGridTable tbody td{font-size:13px}table.greyGridTable tr:nth-child(even){background:#f1f1f1}table.greyGridTable thead{background:#ffffff;border-bottom:2px solid #333333}table.greyGridTable thead th{font-size:15px;font-weight:bold;color:#333333;text-align:center;border-left:0px solid #333333}table.greyGridTable thead th:first-child{border-left:none}table.greyGridTable tfoot td{font-size:14px}

